import Vue from 'vue'
import VueRouter from 'vue-router'
import CovidHome from '@/views/CovidHome.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'CovidHome',
    component: CovidHome
  },
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
